import "../../styles/main.scss"
import SmallInfoHorizontal from "../Home/SmallInfo-horizontal"
import BlogTitle from "./BlogTitle"
import BlogSapo from "./BlogSapo"
import BlogHeading from "./BlogHeading"
import BlogContent from "./BlogContent"
import BlogQuote from "./BlogQuote"
import SimpleCard from "../ui/card/NoTextCard"
import CommentInput from "./CommentInput"
import Comments from "./Comments"
import BlogButton from "./BlogButton"
const BlogPage = () => {
    const title = "Vì sao Zane Navratil được bổ nhiệm làm Chủ tịch Ủy ban VĐV chuyên nghiệp Pickleball?";
    const sapo = "Với kinh nghiệm thi đấu, khả năng kinh doanh và kỹ năng truyền thông hiếm có, Zane Navratil có thể mang đến cho các tay vợt chuyên nghiệp điều mà họ chưa từng có.";
    const content = "Một bước ngoặt quan trọng đối với làng pickleball chuyên nghiệp, Zane Navratil đã được bầu làm Chủ tịch Ủy ban Vận động viên Chuyên nghiệp (Pro Player Committee) của Hiệp hội Pickleball Thống nhất (UPA) – tổ chức chung của PPA Tour và Major League Pickleball (MLP). Đây được xem là nỗ lực lớn nhất từ trước đến nay nhằm thiết lập một cơ chế đại diện thống nhất cho các vận động viên chuyên nghiệp trong môn thể thao đang phát triển bùng nổ này.";
    const heading = "Bước tiến cho tiếng nói của vận động viên";
    const content1 = "Ủy ban Vận động viên của UPA được thành lập để mang lại một tiếng nói có tổ chức và chính thức cho giới vận động viên giữa bối cảnh pickleball chuyên nghiệp phát triển nhanh chóng.";
    const content2 = "Trong nhiều năm, pickleball đã tăng trưởng vượt xa nền tảng tổ chức của nó. Dù PPA hay MLP từng có hội đồng cố vấn, nhưng một tổ chức đại diện chung, có quyền lực thực tế, vẫn chưa từng tồn tại. Trong khi đó, nhu cầu về một cơ quan thống nhất ngày càng cấp thiết khi lịch thi đấu dày đặc, di chuyển tốn kém và quyền lợi thương mại của các giải đang ảnh hưởng trực tiếp đến sự nghiệp và sức khỏe của vận động viên.";
    const heading2 = "Vì sao lại là Zane Navratil?";
    const content3 = "Việc Zane được chọn làm Chủ tịch là điều dễ hiểu. Anh là một trong những tay vợt hàng đầu, vô địch ở cả PPA và MLP, đồng thời là đội trưởng của New Jersey 5s. Ngoài thành tích thi đấu, Zane còn là đối tác kinh doanh của Paddletek và ProXR Pickleball, người sáng lập chuỗi huấn luyện quốc tế, và người dẫn chương trình podcast “PicklePod” – kênh phân tích hàng đầu của The Dink.";
    const quote = {
        quote: "“Đã đến lúc có một tiếng nói mang tính hợp tác, định hướng giải pháp thay vì đối đầu.” ",
        name: "- Zane Navratil"
    }
    const content4 = "Bên cạnh thành tích thi đấu, nền tảng chuyên môn về kế toán của Zane cũng giúp anh có lợi thế trong các cuộc trao đổi với lãnh đạo giải đấu và nhà tài trợ."

    return (
        <div>
            <div className="blog">
                <div className="blog__container">
                    <img src="/assets/image.webp" alt="" className="blog__image w-full" />
                    <div className="blog__user">

                        <div className="blog__user--inner">
                            <SmallInfoHorizontal avatar="/assets/Rectangle 1.webp" name="Kiet Phan" time="10 phút trước" official={true} />

                            <div className="blog__cat">
                                Pickleball
                            </div>
                        </div>

                    </div>
                </div>
                <div className="blog__body">
                    <p><strong>Một chương mới tại Mỹ v&agrave; cơ hội lịch sử</strong></p>
                    <p>Sau một m&ugrave;a giải b&ugrave;ng nổ trong m&agrave;u &aacute;o Inter Miami v&agrave; gi&uacute;p đội tuyển Argentina giữ vững vị thế số một tại v&ograve;ng loại World Cup, Lionel Messi đ&atilde; ch&iacute;nh thức lọt v&agrave;o danh s&aacute;ch đề cử cuối c&ugrave;ng cho giải thưởng Rey de Am&eacute;rica (Vua b&oacute;ng đ&aacute; Mỹ). Đ&acirc;y l&agrave; giải thưởng uy t&iacute;n do tờ b&aacute;o El Pa&iacute;s của Uruguay tổ chức từ năm 1986 nhằm vinh danh cầu thủ xuất sắc nhất đang thi đấu tại c&aacute;c c&acirc;u lạc bộ thuộc hệ thống b&oacute;ng đ&aacute; Nam Mỹ v&agrave; l&acirc;n cận.</p>
                    <p>L&yacute; do Messi chưa từng chạm tay v&agrave;o danh hiệu n&agrave;y rất đơn giản: suốt sự nghiệp đỉnh cao, anh thi đấu tại ch&acirc;u &Acirc;u cho Barca v&agrave; PSG. Chỉ khi chuyển sang MLS, El Pulga mới đủ điều kiện tranh giải. Năm ngo&aacute;i, anh chỉ xếp thứ 5, nhưng năm nay kịch bản đ&atilde; ho&agrave;n to&agrave;n thay đổi.</p>
                    <figure className="image"><img src="https://cdnmedia.webthethao.vn/uploads/2025-12-24/messi.jpg" />
                        <figcaption></figcaption>
                    </figure>
                    <p><strong>Những con số biết n&oacute;i v&agrave; đối thủ đ&aacute;ng gờm</strong></p>
                    <p>Trong năm qua, Messi đ&atilde; thể hiện phong độ hủy diệt với 38 b&agrave;n thắng v&agrave; 25 đường kiến tạo sau 40 trận. Tuy nhi&ecirc;n, cuộc đua năm nay kh&ocirc;ng hề dễ d&agrave;ng khi anh phải đối đầu với hai c&aacute;i t&ecirc;n xuất sắc:</p>
                    <p>Adri&aacute;n &ldquo;Maravilla&rdquo; Mart&iacute;nez: "S&aacute;t thủ" của Racing Club, người vừa gi&agrave;nh Si&ecirc;u c&uacute;p Nam Mỹ v&agrave; l&agrave; vua ph&aacute; lưới Copa Libertadores.</p>
                    <p>Giorgian de Arrascaeta: Nhạc trưởng của Flamengo, người vừa th&acirc;u t&oacute;m cả Copa Libertadores lẫn chức v&ocirc; địch Brazil.</p>
                    <p><strong>Chờ đợi khoảnh khắc vinh danh</strong></p>
                    <p>Lịch sử giải thưởng n&agrave;y từng vinh danh những huyền thoại như Carlos Tevez (3 lần), Neymar hay Ronaldinho. Nếu chiến thắng v&agrave;o ng&agrave;y 31/12 tới đ&acirc;y th&ocirc;ng qua cuộc bầu chọn của hơn 250 nh&agrave; b&aacute;o, Messi sẽ bổ sung mảnh gh&eacute;p cuối c&ugrave;ng v&agrave;o bảng th&agrave;nh t&iacute;ch v&ocirc; tiền kho&aacute;ng hậu của m&igrave;nh.</p>
                    <p>B&ecirc;n cạnh giải thưởng cho cầu thủ, hạng mục Huấn luyện vi&ecirc;n xuất sắc nhất cũng g&acirc;y ch&uacute; &yacute; với sự cạnh tranh giữa Filipe Lu&iacute;s, Gustavo Alfaro v&agrave; Gustavo Costas. Thế giới b&oacute;ng đ&aacute; đang n&iacute;n thở chờ xem liệu "Nh&agrave; vua" c&oacute; thực sự thống trị được cả v&ugrave;ng đất Mỹ hay kh&ocirc;ng.</p>

                </div>

                <img src="/assets/image.webp" alt="" className="blog__image--inner w-full" />
                <div className="blog__body">
                    <BlogHeading heading={heading2} />
                    <BlogContent content={content3} />
                    <BlogQuote quote={quote.quote} name={quote.name} />
                    <BlogContent content={content4} />
                    <div className="blog__btn-container">
                        <BlogButton number={2} text="Bình luận" />
                        <BlogButton number={10} text="Chia sẻ" />
                    </div>
                </div>
                <div className="blog__comment">
                    <CommentInput />
                    <Comments avatar="/assets/Image (3).webp" name="Huong Nguyen" content="Anna hướng dẫn chi tiết, sửa tư thế nhẹ nhàng, giúp tôi thấy cơ thể dẻo dai hơn chỉ sau 2 tháng." time="10 phút trước" />
                </div>
            </div>
        </div>
    )
}
export default BlogPage